@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap);
body {
  font-family: "Roboto Condensed", sans-serif !important;
}

:root {
  --primary-white: #fff;
  --primary-light-grey: #f4f4f4;
  --primary-grey: #ccd1d9;
  --primary-dark-grey: #666666;
  --primary-orange: #f9ab00;
  --primary-dark: rgb(52, 58, 64);
  --primary-red: #ef4035;
  --primary-hover-red: #ef4035;
  --primary-green: green;
  --primary-black: #000000;
  --primary-transparent-black: rgba(0, 0, 0, 0.7);
}

/********NavBar**********/

.mylogo {
  width: 16.25rem;
}

img {
  height: 4rem !important;
}

/********aboutme**********/
.photo-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
}

.profile-img {
  width: 12rem !important;
  height: 12rem !important;
  border-radius: 50%;
  padding: 0.3rem;
  border: 1px solid #f9ab00;
  border: 1px solid var(--primary-orange);
}

.aboutme-heading {
  color: #f9ab00;
  color: var(--primary-orange);
  text-transform: uppercase;
}

@media (max-width: 468px) {
  .profile-img {
    width: 10rem;
  }
}

/*-------Menu Links-------*/
nav a.nav-link {
  font-size: 0.8rem;
  letter-spacing: 0.0375rem;
  cursor: pointer;
}

nav ul li {
  text-transform: uppercase;
}

.navbar-nav > li > a {
  margin-right: 0.75rem;
  margin-left: 0.75rem;
  color: #fff !important;
  color: var(--primary-white) !important;
  border-top: 0.1875rem solid rgb(52, 58, 64);
  border-top: 0.1875rem solid var(--primary-dark);
}

.navbar-brand {
  max-width: 16.25rem;
  padding: 0 !important;
  height: 4rem !important;
}

@media (max-width: 768px) {
  .navbar-brand {
    width: 7rem;
  }
}

.navbar-nav > li > a.active,
.navbar-nav > li > a:focus {
  border-top: 0.1875rem solid #f9ab00;
  border-top: 0.1875rem solid var(--primary-orange);
}

.navbar-nav > li > a:hover {
  border-top: 0.1875rem solid #f9ab00;
  border-top: 0.1875rem solid var(--primary-orange);
}

/********Header**********/
.header-wrapper {
  position: relative;
  background: url(/akhil-kondabathini-website/static/media/mybackground.b18fd445.jpg) no-repeat;
  background-size: cover;
  background-position: center;
  height: 100vh;
}

.main-info {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-family: "Roboto Condensed" sans-serif;
  z-index: 1;
}

.main-info h1 {
  color: #f9ab00;
  color: var(--primary-orange);
  text-transform: uppercase;
  -webkit-filter: brightness(150%);
          filter: brightness(150%);
}

/********Typed-Text**********/

.typed-text {
  font-size: 2rem;
  color: #fff;
  color: var(--primary-white);
}

@media (max-width: 768px) {
  .main-info h1 {
    font-size: 1.7rem !important;
  }
  .typed-text {
    font-size: 1.3rem !important;
  }
}

/********Header Main Offer Button**********/
.btn-main-offer {
  border: 1px solid #ef4035 !important;
  border: 1px solid var(--primary-red) !important;
  text-transform: uppercase;
  border-radius: 0;
  padding: 0.625rem 0.75rem 0.75rem 0.75rem;
  color: #fff !important;
  color: var(--primary-white) !important;
  margin: 2rem;
}

.btn-main-offer:hover {
  text-decoration: none;
  background-color: #ef4035 !important;
  background-color: var(--primary-hover-red) !important;
  color: #fff !important;
  color: var(--primary-white) !important;
  transition: 0.2s ease-in-out;
}

/********Particles**********/
.tsparticles-canvas-el {
  position: absolute;
  height: 100vh;
  width: 100vw;
  z-index: 1;
}

@media (max-width: 768px) {
  .particles-canvas {
    display: none;
  }
}

/********Projects**********/
.projects {
  text-align: center;
  padding-bottom: 2rem;
  background: rgb(52, 58, 64);
  background: var(--primary-dark);
}

.projects h1 {
  color: #f9ab00;
  color: var(--primary-orange);
  text-transform: uppercase;
}
.projects .circle {
  position: relative;
  margin: 0.375rem auto;
  background: #f9ab00;
  background: var(--primary-orange);
  border-radius: 50%;
  width: 3.125rem;
  height: 3.125rem;
}

.projects .icon {
  font-size: 1.4rem;
  color: #fff;
  color: var(--primary-white);
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.projects .box {
  height: 16rem;
  border-bottom: 5px solid #f9ab00;
  border-bottom: 5px solid var(--primary-orange);
  padding: 0.625rem;
  margin-bottom: 1.875rem;
  background: #fff;
  background: var(--primary-white);
  transition: 0.3s ease-in-out;
}

.projects .box:hover {
  background: #f9ab00;
  background: var(--primary-orange);
  border-bottom: 0.3125rem solid rgb(52, 58, 64);
  border-bottom: 0.3125rem solid var(--primary-dark);
}

.projects .box:hover .icon {
  color: rgb(52, 58, 64);
  color: var(--primary-dark);
}

.projects .box:hover .circle {
  background: #fff;
  background: var(--primary-white);
}

.projects .box:hover .p {
  color: #fff;
  color: var(--primary-white);
}

/********Timeline**********/

.timeline {
  padding: 0.7rem;
  background: #ccd1d9;
  background: var(--primary-grey);
}

.timeline-wrapper {
  position: relative;
  padding: 3.125rem;
  overflow: hidden;
}

.timeline h1 {
  text-transform: uppercase;
  color: rgba(0, 0, 0, 0.7);
  color: var(--primary-transparent-black);
}

.timeline-wrapper:before {
  position: absolute;
  content: "";
  top: 0;
  left: 50%;
  margin-left: -0.0625rem;
  width: 0.125rem;
  height: 100%;
  background: #ccd1d9;
  background: var(--primary-grey);
  z-index: 1;
}

.timeline-block {
  width: calc(50% + 0.5rem);
  display: flex;
  justify-content: space-between;
  clear: both;
}

.timeline-block-right {
  float: right;
}

.timeline-block-left {
  float: left;
  direction: rtl;
}

.marker {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  border: 0.3125rem solid rgb(52, 58, 64);
  border: 0.3125rem solid var(--primary-dark);
  background: #f9ab00;
  background: var(--primary-orange);
  margin-top: 0.625rem;
  z-index: 3;
}

.timeline-content {
  width: 95%;
  padding: 0 0.9375rem;
  color: #000000;
  color: var(--primary-black);
  direction: ltr;
}

.timeline-content h3 {
  margin-top: 0.3125rem;
  color: #ef4035;
  color: var(--primary-red);
  font-weight: 900;
}
/*---------------Media----------------*/
@media (max-width: 768px) {
  .timeline .container {
    padding-left: 0;
  }
  .timeline-wrapper:before {
    left: 0.5rem;
    margin-bottom: 1.875rem;
  }
  .timeline-block-right {
    float: none;
    width: 100%;
  }
  .timeline-block-left {
    float: none;
    direction: ltr;
    width: 100%;
  }
}

/*---------------Contact Me----------------*/

.contact {
  background: rgb(52, 58, 64);
  background: var(--primary-dark);
  padding: 3rem 0;
}

.contact h1 {
  color: #f9ab00;
  color: var(--primary-orange);
  padding: 1.3rem 0;
}

.contact p {
  color: #f4f4f4;
  color: var(--primary-light-grey);
  font-size: 1.2rem;
}

/*---------------Inputs----------------*/
.contact input,
.contact textarea {
  width: 100%;
  box-sizing: border-box;
  background: none;
  resize: none;
  border: 0;
  border-bottom: 0.125rem solid #666666;
  border-bottom: 0.125rem solid var(--primary-dark-grey);
  border-radius: 0;
  color: #f9ab00;
  color: var(--primary-orange);
  outline: 0 !important;
  box-shadow: none !important;
  margin-bottom: 1rem;
  padding-left: 0 !important;
}

.contact .container {
  padding: 4rem 0;
}

.contact input:focus,
.contact textarea:focus {
  background: transparent;
  color: #f9ab00;
  color: var(--primary-orange);
  border-bottom: 0.125rem solid #666666;
  border-bottom: 0.125rem solid var(--primary-dark-grey);
}
.contact textarea {
  height: 12.2rem !important;
}

.contact-btn {
  margin: 1rem 0 0 0;
  width: 100%;
  background: none;
}

.line {
  position: relative;
  top: -0.8rem;
  width: 0;
  height: 0.125rem;
  background: #f9ab00;
  background: var(--primary-orange);
  display: inline-block;
}

input:focus + .line,
textarea:focus + .line {
  width: 100%;
  transition: width 0.5s ease-in-out;
}

input.form-control {
  margin: -0.3rem 0;
}

textarea.form-control {
  margin: -0.3rem 0;
}

.error-message {
  color: #fff;
  color: var(--primary-white);
  background: #ef4035;
  background: var(--primary-hover-red);
  text-align: left;
}

.success-message {
  font-weight: 900;
  color: green;
  color: var(--primary-green);
}
/*---------------Media----------------*/
@media (max-width: 768px) {
  .contact {
    overflow-x: hidden;
  }
  .contact .container {
    padding: 0 1rem;
  }
  .contact p {
    padding: 0 0.4rem;
  }
}

/*---------------Footer----------------*/
.footer {
  background: #000000;
  background: var(--primary-black);
  color: #666666;
  color: var(--primary-dark-grey);
  padding: 3rem;
}

.footer a {
  text-decoration: none;
  color: #fff;
  color: var(--primary-white);
  -webkit-filter: brightness(85%);
          filter: brightness(85%);
}

.footer a:hover {
  text-decoration: none;
  color: #f9ab00;
  color: var(--primary-orange);
  cursor: pointer;
}

.footer a.social {
  margin: 0 1rem;
  transition: -webkit-transform 250ms;
  transition: transform 250ms;
  transition: transform 250ms, -webkit-transform 250ms;
  display: inline-block;
}

.footer a.social:hover {
  -webkit-transform: translateY(-3px);
          transform: translateY(-3px);
}

.footer a.linkedin {
  color: #0e76a8;
}

.footer a.youtube {
  color: #eb3223;
}

.footer a.facebook {
  color: #4968ad;
}

.footer a.twitter {
  color: #49a1eb;
}

.footer a.instagram {
  color: white;
}

.footer a.github {
  color: white;
}

.footer p {
  padding: 0 !important;
}

